.Form {
  display: flex;
  flex-direction: column;
  outline: 1px solid black;
  padding: 30px 105px;
}

.Label {
  margin-bottom: 5px;
}

.Input {
  margin-bottom: 30px;
  outline: none;
  padding: 5px 15px;

  &:focus,
  &:hover {
    border: 2px solid rgb(131, 188, 202);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  }
}

button {
  cursor: pointer;
  padding: 5px 15px;

  &:focus,
  &:hover {
    background-color: rgb(217, 233, 238);
  }
}
